/* eslint-disable */
import React, { useEffect, useState } from 'react';
import './SixPointBase.css';

import Clickable from "../base components/Clickable/Clickable";
import Draggable from "../base components/Draggable/Draggable";
import { rgbToHex } from '@material-ui/core';

const AutoTransformer = ({ id, label, data, register, type = 'autotransformer', color = '#3cb878', onMove, top, left }) => {
    const [state, setState] = useState([
        { id: `clickable-1-${type}-${id}`, label, clicked: false },
        { id: `clickable-2-${type}-${id}`, label, clicked: false },
        { id: `clickable-3-${type}-${id}`, label, clicked: false },
        { id: `clickable-4-${type}-${id}`, label, clicked: false },
        { id: `clickable-5-${type}-${id}`, label, clicked: false },
        { id: `clickable-6-${type}-${id}`, label, clicked: false },
    ]);

    const [click1, setClick1] = useState(null);
    const [click2, setClick2] = useState(null);
    const [click3, setClick3] = useState(null);
    const [click4, setClick4] = useState(null);
    const [click5, setClick5] = useState(null);
    const [click6, setClick6] = useState(null);

    useEffect(() => {
        register(state);
    }, [state]);

    useEffect(() => {
        if (data[`clickable-1-${type}-${id}`] != undefined) {
            // if(type === 'autotransformer'){
                setClick1(data[`clickable-1-${type}-${id}`]);
                setClick2(data[`clickable-2-${type}-${id}`]);
                setClick3(data[`clickable-3-${type}-${id}`]);
                setClick4(data[`clickable-4-${type}-${id}`]);
                setClick5(data[`clickable-5-${type}-${id}`]);
                setClick6(data[`clickable-6-${type}-${id}`]);
            // }
            // else{
            //     setClick1(data[`clickable-1-${type}-${id}`]);
            //     setClick2(data[`clickable-2-${type}-${id}`]);
            //     setClick3(data[`clickable-3-${type}-${id}`]);
            //     setClick4(data[`clickable-4-${type}-${id}`]);
            //     setClick5(data[`clickable-5-${type}-${id}`]);
            //     setClick6(data[`clickable-6-${type}-${id}`]);
            // }
        }
        else {
            setClick1(state[0]);
            setClick2(state[1]);
            setClick3(state[2]);
            setClick4(state[3]);
            setClick5(state[4]);
            setClick6(state[5]);
        }
    }, [data]);

    if (click1 && click2) {
        return (
            <Draggable id={id} onMove={onMove} top={top} left={left}>
                <div id={`sixpointbase-${id}`} className="at-container" style={{ backgroundColor: '#3cb878', justifyContent:'center' }}>
                    <div id={`sixpointbase-${id}`} className="at-top">
                        <Clickable
                            data={{ id: click1.id, label: ['1'], clicked: click1.clicked }}
                        />
                        <Clickable
                            data={{ id: click2.id, label: ['2'], clicked: click2.clicked }}
                        />
                        <Clickable
                            data={{ id: click3.id, label: ['3'], clicked: click3.clicked }}
                        />
                    </div>
                    <div id={`sixpointbase-${id}`} style={{ color: 'white',textAlign: 'center', fontSize:'1vw' }}>{label}</div>
                    <div id={`sixpointbase-${id}`} className="at-bottom">
                        <Clickable
                            data={{ id: click4.id, label: ['4'], clicked: click4.clicked }}
                        />
                        <Clickable
                            data={{ id: click5.id, label: ['5'], clicked: click5.clicked }}
                        />
                        <Clickable
                            data={{ id: click6.id, label: ['6'], clicked: click6.clicked }}
                        />
                    </div>
                </div>
            </Draggable>
        );

    }
    return null;
};

// export default SixPointBase;


// const AutoTransformer = ({ id, label, data, register, onMove }) => {
//     return (
//         <SixPointBase id={id} label={label} data={data} type="autotransformer" color="lightgrey" register={register} onMove={onMove} />
//     )
// };

export default AutoTransformer;
