/* eslint-disable */
import React from 'react';
import SixPointBase from '../base components/SixPointBase/SixPointBase';

const OnDelay = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <SixPointBase id={id} label={label} data={data} type="ondelay" color="#598bb5" register={register} onMove={onMove} top={top} left={left} />
    )
};

export default OnDelay;
