/* eslint-disable */
import React from 'react';
import MotorBase from '../base components/MotorBase/MotorBase';

const points = {p1: ['Z2'], p2: ['U2'], p3: ['V2'], p4: ['U1'], p5: ['V1'], p6: ['Z1'], p7: []}
const OnePhaseMotor = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <MotorBase id={id} label={label} data={data} type="onephasemotor" color="#7bb9bd" register={register} phase="1" onMove={onMove} pointLabels={points} top={top} left={left} />
    )
};

export default OnePhaseMotor;
