/* eslint-disable */
import React, { useEffect, useState } from 'react';
import './Resistor.css';

import Clickable from '../base components/Clickable/Clickable';
import Draggable from '../base components/Draggable/Draggable';

const points = {p1: ['1'], p2: ['2'], p3: ['3'], p4: ['4'], p5: ['5'], p6: ['6']};
const movePointLeft = {position: 'relative', left: '-9px'};
const movePointRight = {position: 'relative', right: '-9px'};

const Resistor = ({ id, label, data, register, type = 'resistor', color = '#7accc8', onMove, pointLabels = points , top, left}) => {
    const [state, setState] = useState([
        { id: `clickable-1-${type}-${id}`, label, clicked: false },
        { id: `clickable-2-${type}-${id}`, label, clicked: false },
        { id: `clickable-3-${type}-${id}`, label, clicked: false },
        { id: `clickable-4-${type}-${id}`, label, clicked: false },
        { id: `clickable-5-${type}-${id}`, label, clicked: false },
        { id: `clickable-6-${type}-${id}`, label, clicked: false },
    ]);
    
    const [click1, setClick1] = useState(null);
    const [click2, setClick2] = useState(null);
    const [click3, setClick3] = useState(null);
    const [click4, setClick4] = useState(null);
    const [click5, setClick5] = useState(null);
    const [click6, setClick6] = useState(null);
    
    useEffect(() => {
        register(state);
    }, [state]);
    
    useEffect(() => {
        if (data[`clickable-2-${type}-${id}`] != undefined)
        {
            setClick1(data[`clickable-1-${type}-${id}`]);
            setClick2(data[`clickable-2-${type}-${id}`]);
            setClick3(data[`clickable-3-${type}-${id}`]);
            setClick4(data[`clickable-4-${type}-${id}`]);
            setClick5(data[`clickable-5-${type}-${id}`]);
            setClick6(data[`clickable-6-${type}-${id}`]);
        }
        else
        {
            setClick1(state[0]);
            setClick2(state[1]);
            setClick3(state[2]);
            setClick4(state[3]);
            setClick5(state[4]);
            setClick6(state[5]);
        }
    }, [data,state]);
    
    if (click1 && click2) {
        return (
            <Draggable id={id} onMove={onMove} top={top} left={left}>
            <div id={`resistor-${id}`} className="resistor-container" >
                <div id={`resistor-${id}`} className="resistor-top" style={{backgroundColor: color}}>
                    <Clickable
                        data={{ id: click1.id, label: pointLabels.p1, clicked: click1.clicked}}
                        style={movePointLeft}
                    />
                    <Clickable
                        data={{ id: click4.id, label: pointLabels.p4, clicked: click4.clicked}}
                        style={movePointRight}
                    />
                </div>
                <div id={`resistor-${id}`} className="resistor-middle" style={{backgroundColor: color}} >
                <Clickable
                        data={{ id: click2.id, label: pointLabels.p2, clicked: click2.clicked}}
                        style={movePointLeft}
                    />
                    <div id={`resistor-${id}`} style={{color: 'white',fontSize:'1vw'}}>{label}</div>
                    <Clickable
                        data={{ id: click5.id, label: pointLabels.p5, clicked: click5.clicked}}
                        style={movePointRight}
                    />   
                </div>
                <div id={`resistor-${id}`} className="resistor-bottom" style={{backgroundColor: color}}>
                <Clickable
                        data={{ id: click3.id, label: pointLabels.p3, clicked: click3.clicked}}
                        style={movePointLeft}
                    />
                    <Clickable
                        data={{ id: click6.id, label: pointLabels.p6, clicked: click6.clicked}}
                        style={movePointRight}
                    />
                </div>
            </div>
            </Draggable>
        );
        
    }
    return null;
};

export default Resistor;
