import React from 'react';
import SixPointBase from '../base components/SixPointBase/SixPointBase';

const OffDelay = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <SixPointBase id={id} label={label} data={data} color="#357bb5" type="offdelay" register={register} onMove={onMove} top={top} left={left}/>
    )
};

export default OffDelay;
