/* eslint-disable */
import React from 'react';
import TwoPointBase from '../base components/TwoPointBase/TwoPointBase';

const StopButton = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <TwoPointBase id={id} label={label} data={data} type="stopbutton" color="#d4423a" register={register} onMove={onMove} top={top} left={left}/>
    )
};

export default StopButton;
