/* eslint-disable */
import React from 'react';
import TwoPointBase from '../base components/TwoPointBase/TwoPointBase';

const StartButton = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <TwoPointBase id={id} label={label} data={data} type="startbutton" color="#A0C55F" register={register} onMove={onMove} top={top} left={left}/>
    )
};

export default StartButton;
