import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  AppBar,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Typography
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";

import { makeStyles } from "@material-ui/core/styles";
import { AccountCircle } from "@material-ui/icons";
import { withFirebase } from "../../Firebase";

const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2)
  }
  // title: {
  //     flexGrow: 1,
  // },
}));

const Navigation = props => {
  const classes = useStyles();

  const [auth, setAuth] = useState(null);
  const [anchorElement, setAnchorElement] = useState(null);

  const menuOpen = Boolean(anchorElement);

  const handleMenu = e => {
    setAnchorElement(e.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorElement(null);
  };

  const handleHome = () => {
    props.history.push("/student");
  };

  const handleDesigner = () => {
    props.history.push("/designer");
  };

  const handleLogout = async () => {
    await props.firebase.doSignOut();
    props.history.push("/");
  };

  useEffect(() => {
    setAuth(JSON.parse(sessionStorage.getItem("auth")));
  }, []);

  return (
    <AppBar position="static" style={{ background: "#FFFFFF" }}>
      <Toolbar
        style={{
          display: "flex",
          flexDirection: "row",
          alignContent: "center",
          justifyContent: "space-between"
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignContent: "center"
          }}
        >
          <IconButton
            edge="start"
            className={classes.menuButton}
            style={{ color: "#0C6030" }}
            aria-label="menu"
            onClick={event => props.onMenuIconClick(event)}
          >
            <MenuIcon fontSize="large" />
          </IconButton>
          <Typography variant="h6" style={{ color: "#30600C", margin: "auto" }}>
            {props.pageTitle}
          </Typography>
        </div>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="700"
          height="40"
          viewBox="0 0 2311 729"
        >
          <image
            id="Background"
            width="2311"
            height="729"
            href="data:img/png;base64,"
          />
        </svg>
        <div width="33.33%">
          {auth !== null && (
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                alignContent: "flex-end",
                justifyContent: "flex-end"
              }}
            >
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                style={{ color: "#30600C" }}
              >
                <AccountCircle fontSize="large" />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElement}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right"
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right"
                }}
                open={menuOpen}
                onClose={handleCloseMenu}
              >
                {props.history.location.pathname === "/student" && (
                  <MenuItem onClick={handleDesigner}>Designer View</MenuItem>
                )}
                {props.history.location.pathname === "/designer" && (
                  <MenuItem onClick={handleHome}>Student View</MenuItem>
                )}
                <MenuItem onClick={handleLogout}>Logout</MenuItem>
              </Menu>
            </div>
          )}
        </div>
      </Toolbar>
    </AppBar>
  );
};

export default withRouter(withFirebase(Navigation));
