import React, { useEffect, useState, useRef } from "react";
import Breaker from "../electrical-components/Breaker/Breaker";
import BreakerPoll from "../electrical-components/BreakerPoll/TwoPollBreaker";
import OverloadRelay from "../electrical-components/OverloadRelay/OverloadRelay";
import Contactor from "../electrical-components/Contactor/Contactor";
import OnDelay from "../electrical-components/OnDelay/OnDelay";
import AutoTransformer from "../electrical-components/AutoTransformer/AutoTransformer";
import StartButton from "../electrical-components/StartButton/StartButton";
import StopButton from "../electrical-components/StopButton/StopButton";
import Resistor from "../electrical-components/Resistor/Resistor";
import ThreePhaseMotor from "../electrical-components/ThreePhaseMotor/ThreePhaseMotor";
import OnePhaseMotor from "../electrical-components/OnePhaseMotor/OnePhaseMotor";
import SelectorSwitch from "../electrical-components/SelectorSwitch/SelectorSwitch";
import MotorProtectionCircuitBreaker from "../electrical-components/MPCB/MotorProtectionCircuitBreaker";
import Source from "../electrical-components/base components/Source/Source";
import OffDelay from "../electrical-components/OffDelay/OffDelay";
import "./student.css";
import CheeseburgerMenu from "cheeseburger-menu";
import HamburgerMenu from "react-hamburger-menu";
import { withFirebase } from "../Firebase";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import BreakerI from "../../assets/breaker.png";
import BreakerPollI from "../../assets/2PollBreaker.png";
import ResistorI from "../../assets/Resistor.png";
import AutoTransformerI from "../../assets/Auto Transformer.png";
import ContactorI from "../../assets/Contactor.png";
import MotorProtectionCircuitBreakerI from "../../assets/Motor Protection Circuit Breaker.png";
import OffDelayI from "../../assets/Off Delay.png";
import OnDelayI from "../../assets/On Delay.png";
import OnePhaseMotorI from "../../assets/One phase motor.png";
import OverloadRelayI from "../../assets/Overload relay.png";
import SelectorSwitchI from "../../assets/Selector Switch.png";
import StartI from "../../assets/Start.png";
import StopI from "../../assets/Stop.png";
import ThreePhaseMotorI from "../../assets/Three Phase Motor.png";
import Navigation from "../Layout/Navigation/Navigation";
import ToolTray from "../Layout/ToolTray/ToolTray";
import { useToasts } from "react-toast-notifications";
import ReactHtmlParser from 'react-html-parser';
import {useLocation} from 'react-router-dom';

import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Collapse
} from "@material-ui/core";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import { ExpandMore, ExpandLess, StarBorder } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

const LeaderLine = require("../electrical-components/Line/leader-line");

const useStyles = makeStyles(theme => ({
  root: {
    height: "100%"
  }
}));

const useListStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: "720",
    backgroundColor: theme.palette.background.paper
  },
  nested: {
    paddingLeft: theme.spacing(4)
  },
  list: {
    width: 250
  },
  componentsDrawer: {
    width: 250,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  },
  fullList: {
    width: "auto"
  },
  listItemText: {
    fontSize: "1.2em"
  }
}));

const StudentBase = props => {
  const pathUsed = useLocation().pathname;
  var solutionName = pathUsed.substr(10,useLocation().pathname.length -1)
  //console.log(solutionName)
  const classes = useStyles();
  const listClasses = useListStyles();

  let [objects1, setObjects1] = useState({});
  const [open, setOpen] = React.useState(false);
  const [menuOpen, setmenuOpen] = React.useState(false);
  // const [menuOpen1, setmenuOpen1] = React.useState(false);
  const [lines, setLines] = useState([]);
  const [source, setSource] = useState("");
  const [target, setTarget] = useState("");
  const [connections, setConnections] = useState([]);
  const [toRemove, setToRemove] = useState("");
  const [clickables, setClickables] = useState({});
  const [wireColor, setWireColor] = useState("black");
  let refValue = useRef(wireColor);
  const [ecomponents, setEcomponents] = useState([]);
  const [lcomponents, setLcomponents] = useState([]);
  const [cNames, setCNames] = useState({});
  let refNames = useRef(cNames);
  const [TestCounter,setTestCounter] = useState(0);
  const [RenderCounter,setRenderCounter] = useState(0);
  let refCounter = useRef(TestCounter);
  let refRenderCounter = useRef(RenderCounter);
  const [label, setLabel] = useState("");
  let refLabel = useRef(label);
  const [remove, setRemove] = React.useState(false);
  const [openReset, setOpenReset] = React.useState(false);
  const [openResetScen, setOpenResetScen] = React.useState(false);
  const [problemDescription, setProblemDescription] = useState("");

  // const [loadMemo, setLoadMemo] = React.useState(false);
  //let obj = JSON.parse(sessionStorage.getItem("auth"));

  const [problemName, setProblemName] = useState("");

  const [expandList, setExpandList] = useState(false);
  const [expandLecturerList, setExpandLecturerList] = useState(false);
  const [left, setLeft] = useState(false);
  const [right, setRight] = useState(false);

  const [loadDescription, setLoadDescription] = useState(false);

  const { addToast } = useToasts();

  const handleLoadDescriptionOpen = () => {
    setLoadDescription(true);
  };

  const handleLoadDescriptionClose = () => {
    setLoadDescription(false);
  };

  const toggleDrawer = (side, open) => event => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    if (side === "left") {
      setLeft(open);
    }
    if (side === "right") {
      setRight(open);
    }
  };

  const handleListItemClick = type => {
    if (type === "base") {
      setExpandList(!expandList);
    }
    if (type === "lecturer") {
      setExpandLecturerList(!expandLecturerList);
    }
  };

  // Draw the connection line between the nodes
  const draw = (start, end, options) => {
    return new LeaderLine(
      LeaderLine.pointAnchor(start),
      LeaderLine.pointAnchor(end),
      options
    );
  };

  const checkClass = (styleClasses, styleClass) => {
    return styleClasses.split(" ").includes(styleClass);
  };

  // Click the contact point
  const handleOnClick = e => {
    setConnectionPoint(e);
  };

  // Sets the isClicked value
  const setClickedById = (id, bool) => {
    setClickables(prev => {
      const obj = prev;
      obj[id].clicked = bool;
      return obj;
    });
  };

  // Defines connections between nodes
  const setConnectionPoint = e => {
    if (e && e.target && isClickable(e.target.id)) {
      const { id, className } = e.target;
      if (checkClass(className, "connected")) {
        if (refValue.current === "orange") {
          setConnections(prevConnection => {
            return prevConnection.filter(connection => {
              const points = connection.split(",");
              const shouldRemain = points[1] !== id && points[2] !== id;
              if (!shouldRemain) {
                const id = connection.split(",")[0];
                const id1 = connection.split(",")[1];
                const id2 = connection.split(",")[2];
                document.getElementById(id1).className = "clickable";
                document.getElementById(id2).className = "clickable";
                setLines(lines =>
                  lines.filter(line => {
                    if (parseInt(id) === line._id) {
                      line.remove();
                      return false;
                    }
                    return true;
                  })
                );
                setClickedById(points[1], false);
                setClickedById(points[2], false);
              }
              return shouldRemain;
            });
          });
          return;
        }
      }
      setSource(prev => {
        if (prev) {
          if (prev === id) {
            setClickedById(id, false);
            return "";
          }
          setTarget(id);
          return prev;
        }
        setClickedById(id, true);
        return id;
      });
    } else {
      if (remove === true) {
        let a = e.target.id.split("-");
        removeComponentFromScreen(a[1]);
      }
    }
  };

  // Removes the lines when they are snipped
  useEffect(() => {
    if (!toRemove) {
      return;
    }
    const id = toRemove.split(",")[0];
    const id1 = toRemove.split(",")[1];
    const id2 = toRemove.split(",")[2];

    document.getElementById(id1).className = "clickable";
    document.getElementById(id2).className = "clickable";
    setLines(lines =>
      lines.filter(line => {
        if (parseInt(id) === line._id) {
          line.remove();
          return false;
        }
        return true;
      })
    );
    setToRemove("");
  }, [toRemove]);

  // Open and close functions for the burger menu
  const openMenu = () => {
    setmenuOpen(true);
  };

  const closeMenu = () => {
    setmenuOpen(false);
  };


  // Draws the physical lines and sets the connections between nodes
  useEffect(() => {
    if (source && target) {
      if (refValue.current !== "ornage") {
        const tempSource = document.getElementById(source);
        setClickedById(source, false);
        tempSource.className = "clickable connected";
        const tempTarget = document.getElementById(target);
        setClickedById(target, false);
        tempTarget.className = "clickable connected";
        if (connections.length !== 0) {
          let found = false;
          connections.forEach(connection => {
            let connect1 = connection.split(",")[1];
            let connect2 = connection.split(",")[2];
            if (
              (source === connect1 && target === connect2) ||
              (target === connect1 && source === connect2)
            ) {
              found = true;
              addToast("Connection already exists between these 2 points.", {
                autoDismiss: true,
                appearance: "warning"
              });
            }
          });
          if (!found) {
            const line = draw(tempSource, tempTarget, {
              color: wireColor,
              size: 3,
              startPlug: "disc",
              endPlug: "disc"
            });

            setLines(prev => [...prev, line]);

            setConnections(prev => {
              return [
                ...new Set([
                  ...prev,
                  [source, target, line._id].sort().join(",")
                ])
              ];
            });

            setSource("");
            setTarget("");
          }
        } else {
          const line = draw(tempSource, tempTarget, {
            color: wireColor,
            size: 3,
            startPlug: "disc",
            endPlug: "disc"
          });

          setLines(prev => [...prev, line]);

          setConnections(prev => {
            return [
              ...new Set([...prev, [source, target, line._id].sort().join(",")])
            ];
          });

          setSource("");
          setTarget("");
        }
      } else {
        addToast("Please select a wire colour.", {
          autoDismiss: true,
          appearance: "warning"
        });
      }
    }
  }, [wireColor, source, target]);

  // Updates connections when changes are made
  useEffect(() => {}, [connections]);

  // Checks if object is a clickable object
  const isClickable = id => {
    if (id) {
      return id.split("-")[0] === "clickable";
    }
    return false;
  };

  // Modifies drawn lines when objects are moved
  const updateLines = () => {
    lines.map(line => line.position());
  };

  // Adds event listeners
  useEffect(() => {
    document.addEventListener("touchend", handleOnClick, false);
    document.addEventListener("dblclick", handleOnClick, false);

    return () => {
      document.removeEventListener("touchend", handleOnClick, false);
      document.removeEventListener("dblclick", handleOnClick, false);
    };
  });

  // Rerender on line changes
  useEffect(() => {}, [lines]);

  // Registers components to state
  const registerComp = state => {
    setClickables(prev => {
      const obj = prev;
      for (let i in state) {
        const clickable = state[i];
        obj[clickable.id] = clickable;
      }
      return obj;
    });
  };

  // Choose wire colour
  const setWire = color => {
    setRemove(false);
    refValue.current = color;
    setWireColor(color);
  };

  // Rerenders page when clickables are changed
  useEffect(() => {}, [clickables]);


  // Possibly trash
  const TestMe = async () => {
    if (isNaN(refCounter)) {
      refCounter = 0;
    }

    if (lcomponents.length === 0) {
      if (TestCounter >= 3) {
        setTestCounter(0)
        addToast("All the hints were displayed, showing the memo.", {
          autoDismiss: true,
          appearance: "success"
        });
        CutLines();
        loadLines();
      } else {
        let lineObject;
        try {
          lineObject = objects1[0];
          lineObject = lineObject.sort();
          let res = [];
          connections.forEach(connection => {
            let con = connection.split(",");
            // eslint-disable-next-line eqeqeq
            con[0] = lines.filter(f => f._id == con[0])[0].color;
            con = [con[0], con[1], con[2]].join(",");
            res.push(con);
            res = res.sort();
          });
          if (
            JSON.stringify(res.sort()) === JSON.stringify(lineObject.sort())
          ) {
            addToast("The answer is correct.", {
              autoDismiss: true,
              appearance: "success"
            });
          } else if (res.length < lineObject.length) {
            refCounter++;
            
            setTestCounter(TestCounter+1)
            let i = 0;
            lineObject.filter(error => {
              if (!res.includes(error)) {
                i++;
                let point1 = error.split(",")[1].split("-")[1];
                let component1 = error.split(",")[1].split("-")[3];
                if (!component1.includes("source"))
                {
                  component1 = component1.replace(/[0-9]/g, "")
                }
                let point2 = error.split(",")[2].split("-")[1];
                let component2 = error.split(",")[2].split("-")[3];
                if (!component2.includes("source"))
                {
                  component2 = component2.replace(/[0-9]/g, "")
                }
                if (i <= 1) {
                  
                  addToast("Missing connection between point " +
                  point1 +
                  " of " +
                  component1 +
                  " & point " +
                  point2 +
                  " of " +
                  component2, {
                      autoDismiss: true,
                      appearance: "warning"
                    })
                }
              }
            });
          } else if (res.length >= lineObject.length) {
            refCounter++;
            setTestCounter(TestCounter+1)
            let i = 0;
            res.filter(error => {
              if (!lineObject.includes(error)) {
                let point1 = error.split(",")[1].split("-")[1];
                let component1 = error.split(",")[1].split("-")[2];
                let point2 = error.split(",")[2].split("-")[1];
                let component2 = error.split(",")[2].split("-")[2];
                if (i <= 1) {
                  res.length > lineObject.length
                    ? addToast("There are to many connection one of which is: point " +
                    point1 +
                    " of " +
                    component1 +
                    " & point " +
                    point2 +
                    " of " +
                    component2, {
                      autoDismiss: true,
                      appearance: "warning"
                    })
                    :
                    addToast("There is a wrong connection between point " +
                    point1 +
                    " of " +
                    component1 +
                    " & point " +
                    point2 +
                    " of " +
                    component2 +
                    point1 +
                    " of " +
                    component1 +
                    " & point " +
                    point2 +
                    " of " +
                    component2, {
                      autoDismiss: true,
                      appearance: "warning"
                    })
                }
              }
            });
          }
        } catch {
          addToast("Answer not set up.", {
            autoDismiss: true,
            appearance: "error"
          });
        }
      }
      let iconButtons = document.getElementsByClassName(
        "MuiIconButton-colorPrimary"
      );
      iconButtons.length > 0
        ? iconButtons[0].classList.remove("MuiIconButton-colorPrimary")
        : console.log("");
    } else {
      addToast("Remove all items from the tray first.", {
        autoDismiss: true,
        appearance: "warning"
      });
    }
    //endelse
  };

  // Loads the components from the database retrieval
  const loadComponent = (val, id, label) => {
    setOpen(false);
    let newval = val.replace(/\s/g, "") + "I";
    let trayComponent;
    switch (newval) {
      default: {
        alert(val.replace(/\s/g, "") + "I");
        break;
      }
      case "AutoTransformerI": {
        trayComponent = AutoTransformerI;
        break;
      }
      case "BreakerI": {
        trayComponent = BreakerI;
        break;
      }
      case "2-PollBreakerI": {
        //console.log(BreakerPollI)
        trayComponent = BreakerPollI;
        break;
      }
      case "ContactorI": {
        trayComponent = ContactorI;
        break;
      }
      case "MotorProtectionCircuitBreakerI": {
        trayComponent = MotorProtectionCircuitBreakerI;
        break;
      }
      case "OffDelayI": {
        trayComponent = OffDelayI;
        break;
      }
      case "OnDelayI": {
        trayComponent = OnDelayI;
        break;
      }
      case "Single-PhaseMotorI": {
        trayComponent = OnePhaseMotorI;
        break;
      }
      case "OverloadRelayI": {
        trayComponent = OverloadRelayI;
        break;
      }
      case "ResistorI": {
        trayComponent = ResistorI;
        break;
      }
      case "SelectorSwitchI": {
        trayComponent = SelectorSwitchI;
        break;
      }
      case "StartButtonI": {
        trayComponent = StartI;
        break;
      }
      case "StopButtonI": {
        trayComponent = StopI;
        break;
      }
      case "Three-PhaseMotorI": {
        trayComponent = ThreePhaseMotorI;
        break;
      }
    }
    let newListComponent = {
      id: id,
      c: val,
      label: label,
      trayComponent: trayComponent
    };
    if (lcomponents.length !== 0) {
      setLcomponents(lcomponents => [...lcomponents, newListComponent]);
    } else {
      lcomponents.push(newListComponent);
      setLcomponents([newListComponent]);
    }
  };

  const removeComponent = id => {
    setLcomponents(components =>
      components.filter(component => component.id !== id)
    );
    return true;
  };

  const removeComponentFromScreen = id => {
    let noRemove = [];
    connections.forEach(connection => {
      let lineid = connection.split(",")[0];
      let connect1 = connection.split(",")[1].split("-")[3];
      let connect2 = connection.split(",")[2].split("-")[3];
      if (connect1 === id) {
        setLines(lines =>
          lines.filter(line => {
            if (parseInt(lineid) === line._id) {
              line.remove();
              return false;
            }
            return true;
          })
        );
      } else if (connect2 === id) {
        setLines(lines =>
          lines.filter(line => {
            if (parseInt(lineid) === line._id) {
              line.remove();
              return false;
            }
            return true;
          })
        );
      } else {
        noRemove.push(connection);
      }
    });
    setConnections(noRemove);
    let remComponent = ecomponents.filter(component => component.id === id);
    remComponent = remComponent[0];
    if (remComponent !== undefined) {
      loadComponent(remComponent.c, remComponent.id, remComponent.label);
      setEcomponents(components =>
        components.filter(component => component.id !== id)
      );
    }
  };

  const loadComponentToScreen = async (val, id, label) => {
    //console.log(val)
    let spaceBetween = 0;
    if (ecomponents.length !== 0) {
      spaceBetween = ecomponents[ecomponents.length - 1].left + 200;
    }
    setOpen(false);
    await removeComponent(id);
    let newComponent = {
      id: id,
      c: val,
      label: label,
      top: 200,
      left: spaceBetween
    };
    if (ecomponents.length !== 0) {
      setEcomponents(ecomponents => [...ecomponents, newComponent]);
    } else {
      ecomponents.push(newComponent);
      setEcomponents([newComponent]);
    }
  };

  // Removes all wires from the sollution
  const CutLines = () => {
    handleCloseReset();
    connections.forEach(conn => {
      const id = conn.split(",")[0];
      const id1 = conn.split(",")[1];
      const id2 = conn.split(",")[2];
      document.getElementById(id1).className = "clickable";
      document.getElementById(id2).className = "clickable";
      setLines(lines =>
        lines.filter(line => {
          if (parseInt(id) === line._id) {
            line.remove();
            return false;
          }
          return true;
        })
      );
    });
    connections.length = 0;
    return true;
  };

  // Gets all the problem names from firebase
  const firebaseGetAll = async val => {
    let names = await props.firebase.getProblemNames(val);
    refNames.current = names;
    setCNames(names);
  };
  const handleCloseReset = () => {
    setOpenReset(false);
  };
  const setReset = () => {
    setOpenReset(true);
  };

  // Loads a specific problem from firebase
  const loadProblem = async val => {
    document.getElementById("blackWire").style.backgroundColor = "black";
    document.getElementById('Delete-Component').style.filter = "grayscale(100%)"
    document.getElementById("sker").style.filter = "grayscale(100%)"
    CutLines();
    ecomponents.length = 0;
    lcomponents.length = 0;
    setEcomponents([]);
    setLcomponents([]);
    objects1 = await props.firebase.getProblem(val);
    if(objects1 === undefined)
    {
      addToast("The scenario was not loaded, please make sure that the scenario exists.", {
        autoDismiss: true,
        appearance: "error"
      });
    }
    else{
    if(objects1[3] === undefined){
      setProblemDescription(undefined)
    }
    else{
      setProblemDescription(objects1[3].description)
    }
    setObjects1(objects1);
    await objects1[1].forEach(async element => {
      refLabel.current = element.label;
      await loadComponent(element.c, element.id, element.label);
    });
    setProblemName(val.split(",")[1]);

    addToast("The scenario was loaded and the components can be found in the components menu on the right.", {
      autoDismiss: true,
      appearance: "success"
    });
    setLeft(false);
    setRight(true);
  }
  };

  // Loads lines from database
  const loadLines = () => {
    let lineObject;
    lineObject = objects1[0];
    lineObject.forEach(element => {
      const color = element.split(",")[0];
      const id1 = element.split(",")[1];
      const id2 = element.split(",")[2];
      const tempSource = document.getElementById(id1);
      // connected
      tempSource.className = "clickable connected";
      const tempTarget = document.getElementById(id2);
      tempTarget.className = "clickable connected";

      const line = draw(tempSource, tempTarget, {
        color: color,
        size: 3,
        startPlug: "disc",
        endPlug: "disc"
      });
      setLines(prev => [...prev, line]);

      setConnections(prev => {
        return [...new Set([...prev, [id1, id2, line._id].sort().join(",")])];
      });
    });
  };

  // On first load for the problem buttons
  // useEffect(() => {
  //   document.getElementById("blackWire").style.backgroundColor = "black";
  // }, []);

  const handleClose = () => {
    setOpen(false);
  };

  const handleChange = e => {
    refLabel.current = e.target.value;
    setLabel(e.target.value);
  };

  useEffect(() => {
    return () => {
      let element = document.getElementsByTagName("svg");
      while (element[0]) element[0].parentNode.removeChild(element[0]);
    };
  }, []);

  const btnRemove = () => {
    setRemove(true);
  };
  const handleCloseResetScen = () => {
    setOpenResetScen(false);
  };


  // Render
  if (clickables) {
    if(solutionName !== '' && refNames.current.base === undefined){
      firebaseGetAll("user")
    }
    if( document.getElementById("blackWire") !== null && solutionName !== '' && refRenderCounter.current <= 0){
      loadProblem("default@test.co," + solutionName) 
      refRenderCounter.current++
    }
    if (refNames.current.base !== undefined) {
      return (
        <div className={classes.root}>
          <Navigation
            onMenuIconClick={toggleDrawer("left", true)}
            pageTitle="Solve a Scenario"
          />
          <ToolTray
            loadMemo={{}}
            onLoadMemoClick={() => {}}
            problemName={problemName}
            onSaveClick={() => {}}
            onTextChange={() => {}}
            setWire={setWire}
            setReset={setReset}
            btnRemove={btnRemove}
            hide
            testable
            testSolution={TestMe}
            handleLoadDescriptionOpen={handleLoadDescriptionOpen}
          />
          <Divider />
          <Drawer open={left} onClose={toggleDrawer("left", false)}>
            <div className={listClasses.list} role="presentation">
              <List
                component="nav"
                aria-labelledby="simulation-list"
                className={listClasses.root}
              >
                <ListItem button onClick={() => handleListItemClick("base")}>
                  <ListItemIcon>
                    <InboxIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary="Base Problems"
                    classes={{ primary: listClasses.listItemText }}
                  />
                  {expandList ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Divider />
                <Collapse in={expandList} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    {refNames.current.base.map(el => (
                      <ListItem
                        key={el}
                        button
                        onClick={() => loadProblem("default@test.co," + el)}
                        className={listClasses.nested}
                      >
                        <ListItemIcon>
                          <StarBorder />
                        </ListItemIcon>
                        <ListItemText
                          primary={el}
                          classes={{ primary: listClasses.listItemText }}
                        />
                      </ListItem>
                    ))}
                  </List>
                </Collapse>
                <ListItem
                  button
                  onClick={() => handleListItemClick("lecturer")}
                >
                  <ListItemIcon>
                    <InboxIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary="Lecturer Problems"
                    classes={{ primary: listClasses.listItemText }}
                  />
                  {expandLecturerList ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Divider />
                <Collapse in={expandLecturerList} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    {refNames.current.lect.map(el => (
                      <ListItem
                        key={el}
                        button
                        onClick={() => loadProblem(refLabel.current + "," + el)}
                        className={listClasses.nested}
                      >
                        <ListItemIcon>
                          <StarBorder />
                        </ListItemIcon>
                        <ListItemText
                          primary={el}
                          classes={{ primary: listClasses.listItemText }}
                        />
                      </ListItem>
                    ))}
                  </List>
                </Collapse>
              </List>
            </div>
          </Drawer>
          <Drawer
            open={right}
            anchor="right"
            onClose={toggleDrawer("right", false)}
          >
            <div className={listClasses.componentsDrawer} role="presentation">
            <span style={{padding:"2%"}}>Click a component to add it.</span>
              {lcomponents.map(component => {
                return (
                  <div style={{display:"flex",flexDirection:"column",justifyContent:"center"}}>
                    <div align="center">
                    <img
                      key={component.id + "img"}
                      src={component.trayComponent}
                      alt={component.c}
                      onClick={() =>
                        loadComponentToScreen(
                          component.c,
                          component.id,
                          component.label
                        )
                      }
                      width="70rem"
                    />
                    </div>
                    <div>
                    <p>{component.label}</p>
                    </div>
                  </div>
                );
              })}
            </div>
          </Drawer>
          <div className="toolbar-right-container">
            <Button variant="outlined" style={{backgroundColor:'#86b59c'}} onClick={toggleDrawer("right", true)}>Components</Button>
          </div>

          {ecomponents.length !== 0 &&
            ecomponents.map(component => {
              let resA;
              switch (component.c) {
                default: {
                  break;
                }
                case "Auto Transformer": {
                  resA = (
                    <AutoTransformer
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Breaker": {
                  resA = (
                    <Breaker
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "2-Poll Breaker": {
                  resA = (
                    <BreakerPoll
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Contactor": {
                  resA = (
                    <Contactor
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Motor Protection Circuit Breaker": {
                  resA = (
                    <MotorProtectionCircuitBreaker
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Off Delay": {
                  resA = (
                    <OffDelay
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "On Delay": {
                  resA = (
                    <OnDelay
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Single-Phase Motor": {
                  resA = (
                    <OnePhaseMotor
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Overload Relay": {
                  resA = (
                    <OverloadRelay
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Resistor": {
                  resA = (
                    <Resistor
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Selector Switch": {
                  resA = (
                    <SelectorSwitch
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Start Button": {
                  resA = (
                    <StartButton
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Stop Button": {
                  resA = (
                    <StopButton
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
                case "Three-Phase Motor": {
                  resA = (
                    <ThreePhaseMotor
                      key={component.id}
                      id={component.id}
                      label={component.label}
                      register={registerComp}
                      data={clickables}
                      onMove={updateLines}
                      top={component.top}
                      left={component.left}
                    />
                  );
                  break;
                }
              }
              return resA;
            })}

          <Dialog
            open={openReset}
            onClose={handleCloseReset}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">
              Reset all connections.
            </DialogTitle>
            <DialogContent>
              <DialogContentText>
                Are you sure you want to reset all the connections?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseReset} color="primary">
                No
              </Button>
              <Button onClick={() => CutLines()} color="primary">
                Yes
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={openResetScen}
            onClose={handleCloseResetScen}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">Reset Scenario.</DialogTitle>
            <DialogContent>
              <DialogContentText>
                There is currently a Scenario on the screen do you want to load
                a new one?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseResetScen} color="primary">
                No
              </Button>
              <Button onClick={() => loadProblem()} color="primary">
                Yes
              </Button>
            </DialogActions>
          </Dialog>
          <Source
            id="source1"
            label="1"
            data={clickables}
            register={registerComp}
            onMove={updateLines}
            left={20}
            color="black"
          />
          <Source
            id="source2"
            label="2"
            data={clickables}
            register={registerComp}
            onMove={updateLines}
            left={60}
            color="blue"
          />
          <Source
            id="source3"
            label="3"
            data={clickables}
            register={registerComp}
            onMove={updateLines}
            left={100}
            color="red"
          />
          <Source
            id="sourceNeutral"
            label="N"
            data={clickables}
            color="#836953"
            type="Neutral"
            register={registerComp}
            onMove={updateLines}
            left={140}
          />
          <Source
            id="sourceEarth"
            label="E"
            data={clickables}
            color="#69BD63"
            type="Earth"
            register={registerComp}
            onMove={updateLines}
            left={180}
          />
          <Dialog
          open={loadDescription}
          onClose={handleLoadDescriptionClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Problem Description</DialogTitle>
          <DialogContent>
            
            { 
              problemDescription === undefined ? 
              <DialogContentText>
                "No description provided" 
              </DialogContentText> : 
              <DialogContent> { ReactHtmlParser (problemDescription) } </DialogContent>
            }
            
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleLoadDescriptionClose()} color="primary">
              Okay
            </Button>
          </DialogActions>
        </Dialog>
        </div>
      );
    } else {
      return (
        <div>
          <CheeseburgerMenu
            isOpen={menuOpen}
            closeCallback={closeMenu}
            right={false}
          >
            <button onClick={() => firebaseGetAll("user")}>Test</button>
            <h1>Base</h1>

            <h1>Lecturer</h1>
          </CheeseburgerMenu>

          {/* <div className="Layout"> */}
          <div className="hamburger">
            <HamburgerMenu
              isOpen={menuOpen}
              menuClicked={openMenu}
              width={32}
              height={24}
              strokeWidth={3}
              rotate={0}
              color="#0C6030"
              borderRadius={0}
              animationDuration={0.5}
            />
          </div>
          {/* </div> */}
          <Source
            id="source1"
            label="1"
            data={clickables}
            register={registerComp}
            onMove={updateLines}
            left={20}
          />
          <Source
            id="source2"
            label="2"
            data={clickables}
            color="blue"
            register={registerComp}
            onMove={updateLines}
            left={60}
          />
          <Source
            id="source3"
            label="3"
            data={clickables}
            color="red"
            register={registerComp}
            onMove={updateLines}
            left={100}
          />
          <Source
            id="sourceNeutral"
            label="N"
            data={clickables}
            color="brown"
            type="Neutral"
            register={registerComp}
            onMove={updateLines}
            left={180}
          />
          <Source
            id="sourceEarth"
            label="E"
            data={clickables}
            color="green"
            type="Earth"
            register={registerComp}
            onMove={updateLines}
            left={140}
          />
          

          <Dialog
            open={true}
            onClose={handleClose}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">
              Load lecture specific problems
            </DialogTitle>
            <DialogContent>
              <DialogContentText>
                Please enter your lectures email address.
              </DialogContentText>
              <TextField
                autoFocus
                margin="dense"
                id="name"
                label="Email"
                type="text"
                fullWidth
                onChange={handleChange}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={() => firebaseGetAll("user")} color="primary">
                Default Only
              </Button>
              <Button
                onClick={() => firebaseGetAll(refLabel.current)}
                color="primary"
              >
                Load Lecturer
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={loadDescription}
            onClose={handleLoadDescriptionClose}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">Problem Description</DialogTitle>
            <DialogContent>
              <DialogContentText>
                { problemDescription === undefined ? "No description provided" : problemDescription}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => handleLoadDescriptionClose()} color="primary">
                Okay
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      );
    }
    
  }
};

const Student = withFirebase(StudentBase);

export default Student;
