import React from 'react';
import Wire from '../../electrical-components/base components/Wire/Wire';
import Test from '../../electrical-components/base components/Test/Test';
import IconBtn from '../../electrical-components/base components/IconBtn/IconBtn';
import SingleCut from '../../electrical-components/base components/SingleCut/SingleCut';
import { Toolbar, TextField, Button } from '@material-ui/core';
import './ToolTray.css';

const ToolTray = props => {

    return (
        <Toolbar style={{backgroundColor:'#86b59c'}}>
            <div className="toolbar-container" >
                <div className="toolbar-left-container">
                <svg xmlns="http://www.w3.org/2000/svg" style={{paddingRight:'2px'}} xlink="http://www.w3.org/1999/xlink" width="30" height="30" viewBox="0 0 100 127">
  <defs>
    <pattern id="pattern" preserveAspectRatio="none" width="100%" height="100%" viewBox="0 0 735 929">
      <image width="735" height="929" href="data:image/png;base64,"/>
    </pattern>
  </defs>
  <rect id="thumbnail" width="100" height="127" fill="url(#pattern)"/>
</svg>

                    <TextField
                        id="outlined-basic"
                        onChange={props.onTextChange}
                        label="Problem Title"
                        variant="outlined"
                        size="small"
                        value={props.problemName}
                        disabled={props.hide}
                        style={{width:'60%', paddingRight:'1%'}}
                    />
                    <div style={{display: props.hide ? 'none' : ''}}>
                        {/* <IconBtn id="Load" onClick={props.onLoadMemoClick} disabled={props.loadMemo == false} icon="get_app" /> */}
                        <IconBtn id="Save" onClick={props.onSaveClick} disabled={props.problemName === ""} icon="save" />
                        <Button id="newProblem" style={{maxWidth:'40%'}} variant="outlined" onClick={props.resetpage}>New Problem</Button>
                        <Button variant="outlined" onClick={props.handleDescriptionOpen}>Add Description</Button>
                    </div>
                    <div style={{ display: props.hide ? "" : "none" }}>
            <Button variant="outlined" onClick={props.handleLoadDescriptionOpen}>View Description</Button>
          </div>
                </div>
                <div className="toolbar-right-container">
                    <Wire color="black" id="blackWire" setWire={props.setWire} />
                    <Wire color="blue" id="blueWire" setWire={props.setWire} />
                    <Wire color="red" id="redWire" setWire={props.setWire} />
                    <Wire color="green" id="greenWire" setWire={props.setWire} />
                    <Wire color="#836953" id="brownWire" setWire={props.setWire} />
                    <Test id="Test" color="orange" TestMe={props.testSolution} hide={!props.testable}/>
                    <IconBtn id="Remove-all-connections" onClick={props.setReset} />
                    <SingleCut id="sker" color="orange" setWire={props.setWire} />
                    <IconBtn id="Delete-Component" onClick={props.btnRemove} icon="delete" />
                </div>
            </div>
        </Toolbar>
    )
            
          
};

export default ToolTray;
