/* eslint-disable */
import React from 'react';
import MotorBase from '../base components/MotorBase/MotorBase';

const ThreePhaseMotor = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <MotorBase id={id} label={label} data={data} type="threephasemotor" color="#40b5bc" register={register} onMove={onMove} top={top} left={left} />
    )
};

export default ThreePhaseMotor;
